.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  background: #7d2ae8;
  height: 100vh;
}
.card{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: #fff;
  max-width: 600px;
  margin: auto;
  border-radius: 30px;
  border: 5px solid #111;
  box-shadow: -15px 10px 10px 10px   rgba(0, 0, 0, 0.5) ;
}

.profile-photo {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 20px;
  
}

.profile-photo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.full-name {
  text-align: center;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 10px;
  color: #7d2ae8;
  border-bottom: 1px solid #111;
}

.address {
  text-align: center;
  font-size: 2rem;
  color: #6b12cb;
  border-bottom: 1px solid #111;

}
